import React from 'react';
import classnames from 'classnames';
import './Board.css';

class Square extends React.Component {
  constructor(props) {
    super(props)
  }

  render() {
    let face;
    let detail;
    
    if (this.props.special){
      if (this.props.num == 0){
        face = 
      <div className="face">
        <b>{this.props.name}</b>
        <br/>
      </div>
  
      detail = 
      <b className={classnames('detail', this.props.provinceColor)}>
        Collect $200!
      </b>;
      }else if ([2,7,17,22,33,36].includes(this.props.num)){
        face = 
          <div className="face">
            <b className="random">?</b>
          </div>
  
        detail = 
          <div className={classnames('detail', this.props.provinceColor)}>
            <b>Random events!</b>
          </div>;
      }else if (this.props.num == 20){
        face = 
      <div className="face">
        <b>{this.props.name}</b>
        <br/>
      </div>
      detail = 
      <b className={classnames('detail', this.props.provinceColor)}>
        Do nothing!
      </b>;
  
      }else if ([10,30].includes(this.props.num)){
        face = 
      <div className="face">
        <b>{this.props.name}</b>
        <br/>
      </div>
  
      detail = 
      <b className={classnames('detail', this.props.provinceColor)}>
        Cargos have arrived! Every bodyguard earns money for unloading.
      </b>;
      }else if (this.props.num == 4){
        face = 
      <div className="face">
        <b>10% tax!</b>
      </div>
      }else{
        face = 
      <div className="face">
        <b>20% tax!</b>
      </div>
      }
    }else if ([5,15,25,35].includes(this.props.num)) {
      face = 
      <div className="face">
        <b className="owner">{this.props.name}</b>
        <br/>
        <i>{this.props.ownerNum}</i>
      </div>;
  
      let rents = [];
      if ( this.props.multiplier > 1){
        rents.push(<b className={classnames("owner", "redText")}>${this.props.rents[0]} x{this.props.multiplier}</b>)
      }else{
        rents.push(<b className={classnames("owner", "redText")}>${this.props.rents[0]}</b>)
      }
      detail = 
      <div className={classnames('detail', this.props.provinceColor)}>
        <b className="owner">{this.props.name}</b>
        <br/>
        <b>Buy price: </b>
        <b className="redText">{this.props.buyPrice ? "$" + this.props.buyPrice : null}</b>
        <br></br>
        <div className="sideBySide">
          <div className="column">
            <b>rents: </b>
            {rents}
          </div>
          <div className="column">
            <b>guards: </b>
            <b className={classnames("owner", "redText")}>{this.props.guards[0]}</b>
          </div>
        </div>
        <b>Owning 2 railways charges 2x rent, 3 railways 4x rent, 4 railways 8x rent.</b>
      </div>
  
    }else if ([12,28].includes(this.props.num)) {
      face = 
      <div className="face">
        <b className="owner">{this.props.name}</b>
        <br></br>
        <i>{this.props.ownerNum}</i>
      </div>;
  
      let rents = [];
      if ( this.props.multiplier > 1){
        rents.push(<b className={classnames("owner", "redText")}>${this.props.rents[0]} x{this.props.multiplier}</b>)
      }else{
        rents.push(<b className={classnames("owner", "redText")}>${this.props.rents[0]}</b>)
      }
      detail = 
      <div className={classnames('detail', this.props.provinceColor)}>
        <b className="owner">{this.props.name}</b>
        <br/>
        <b>Buy price: </b>
        <b className="redText">{this.props.buyPrice ? "$" + this.props.buyPrice : null}</b>
        <br></br>
        <div className="sideBySide">
          <div className="column">
            <b>rents: </b>
            {rents}
          </div>
          <div className="column">
            <b>guards: </b>
            <b className="redText">{this.props.guards[0]}</b>
          </div>
        </div>
        <b>Owning both Electric Company and Water Works charges 2x rent.</b>
      </div>
  
    }else{
      face = 
      <div className="face">
        <b className="owner">{this.props.name}</b>
        <br/>
        <i>{this.props.ownerNum}</i>
        <br></br>
        <b>{this.props.level ? "level: " + this.props.level : null}</b>
      </div>;
  
      let rents = [];
      let guards = [];
      if (this.props.rents){
        for (let idx in this.props.rents){
          if (idx == this.props.level){
            if ( this.props.multiplier > 1){
              rents.push(<b className={classnames("owner", "redText")}>${this.props.rents[idx]} x{this.props.multiplier}</b>)
            }else{
              rents.push(<b className={classnames("owner", "redText")}>${this.props.rents[idx]}</b>)
            }
            
          }else{
            rents.push(<b>${this.props.rents[idx]}</b>)
          }
        }
      }
      if (this.props.guards){
        for (let idx in this.props.guards){
          if (idx == this.props.level){
            guards.push(<b className={classnames("owner", "redText")}>{this.props.guards[idx]}</b>)
          }else{
            guards.push(<b>{this.props.guards[idx]}</b>)
          }
        }
      }
      
      detail = 
      <div className={classnames('detail', this.props.provinceColor)}>
        <b className="owner">{this.props.name}</b>
        <br/>
        <b>Buy price: </b>
        <b className="redText">{this.props.buyPrice ? "$" + this.props.buyPrice : null}</b>
        <br></br>
        <div className="sideBySide">
          <div className="column">
            <b>rents: </b>
            <br></br>
            {rents}
          </div>
          <div className="column">
            <b>guards: </b>
            <br></br>
            {guards}
          </div>
        </div>
        
        <b className="redText">{this.props.upgradePrice ? "upgrade price: $" + this.props.upgradePrice : null}</b>
        <br></br>
        <b>Owning all properties of same color charges 2x rent.</b>
      </div>;
    }
  
    return ( 
    <div className={classnames('square', this.props.styleName, this.props.provinceColor)} id = {this.props.id}>
  
      {face}
  
      {detail}
  
    </div>
  )};
  
}

export default Square;
